.main{
    grid-area:main;
    background-color: var(--component-bg-color);
    box-shadow: var(--boxShadow);
    margin-top:15px;
    border-radius: 1.7em;
}
.aside{
    grid-area: aside;
}
.profileImg{
    grid-area:profileImg;
}

.logoLink{
    grid-area:logo;
}

.workExperienceHeader{
    font-size: 3em;
    font-weight: bold;
    text-align: center;
    padding:.5em;

    color:var(--main-bg-color);
    border-bottom: 1px solid var(--main-bg-color);
}

.jobTitle{
    grid-area:jobTitle;
}

.desc{
    grid-area:desc;
    font-weight: bold;
}

.length{
    grid-area:length;
}
.referenceName{
    grid-area:referenceName;
    text-align: right;
}
.position{
    grid-area:position;
    text-align: right;
}
.company{
    grid-area:company;
    text-align: right;
}
.imgUrl{
    grid-area:imgUrl;
    border-radius: 50%;
    width: 100%;
}

.recommendationCard{
    background-color: var(--component-bg-color);
    border-radius: 1.7em;
    padding:2em;

    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 10em 3fr;

    grid-template-areas: 
    "imgUrl referenceName"
    "imgUrl position"
    "imgUrl company"
    "referenceText referenceText";
}

.recommendationCard:not(:last-child){
    margin-bottom: 1em;
}

.referenceText{
    grid-area:referenceText;
    font-style: italic;
    padding-top: 1.5em;
}

.companyProfile{
    padding: 1em;
    align-items: center;
    display: grid;
    grid-column-gap: 2em;
}

.layout{
    display:grid;
    grid-row-gap:1em;
    grid-column-gap: 1em;
    grid-template-rows: auto;
}

.contactCard{
    grid-area:contactCard;
    box-shadow: var(--boxShadow);
    background-color: var(--component-bg-color);
    border-radius: 1.7em;
    display: grid;
}

@media (max-width:1000px){
    .companyProfile{
        margin:2em;
        border-bottom: 1px solid var(--main-bg-color);
        grid-row-gap:1em;
        grid-template-areas:
        "logo"
        "jobTitle"
        "length"
        "desc";
    }

    .contactCard{
        padding:4em;
        grid-template-columns: auto;
        grid-template-rows: auto;
        grid-row-gap: 2em;
    }

    .layout{
        grid-template-areas: 
        "navbar"
        "contactCard"
        "main"
        "aside";
    }
}

@media (min-width:1001px){
    .companyProfile{
        grid-template-columns: 3fr 5fr 1fr;
        grid-template-rows: 1fr 2fr;
        grid-template-areas: 
        "logo jobTitle length"
        "logo desc     length";
    }

    .layout{
        grid-template-columns: 5fr 4fr;
        grid-template-areas: 
        "navbar      navbar"
        "contactCard aside"
        "main        aside";
    }

    .contactCard{
        padding: 1em 3em 1em 2em;
        grid-template-columns: auto repeat(3, 6em);
        grid-column-gap: 2em;
    }
}